<script>
export default({
    name: 'ZhunClint',
    data(){
          return{
            prospect: {}
          }
        },
        mounted(){
          this.show();
        },
        methods:{
          show(){
            var precustomerno_id = this.$route.query.precustomerno_id;
            var vm = this;
            this.axios({
              method: 'post',
              url: 'http://localhost:8081/selectPerspect?precustomerno_id='+precustomerno_id,
            }).then(function (resp){
              vm.prospect=resp.data
              console.log(resp)
            })
          },
          dele(){
            var precustomerno_id = this.$route.query.precustomerno_id;
            var vm = this;
            this.axios({
              method: 'post',
              url: 'http://localhost:8081/deleteProspect?precustomerno='+precustomerno_id,
            }).then(function (resp){
              vm.$router.push({name: 'StandardClint'})
            })
          }
        }
})

</script>

