                'Post': '',
                'PreCustomerNo': '',
                'Depart': '',
                'Birthday': '',
                'Mobile': '',
            }
            ]
        }
        }
    },
    mounted(){
        this.show();
    },
    methods:{
        show(){
        var precustomerno_id = this.$route.query.precustomerno_id;
        var vm = this;
        this.axios({
            method: 'post',
            url: 'http://localhost:8081/selectPerspect?precustomerno_id='+precustomerno_id,
            }).then(function (resp){
                vm.prospect=resp.data
                console.log(resp)
            })
        },
        updateGTP(){
          var data = this.prospect
          var vm = this;
          console.log(data)
          this.axios.post('http://localhost:8081/updateProspect',
              this.qs.stringify(data,{arrayFormat: 'indices', allowDots: true})).then(function (response) {
              vm.$router.push({name: 'ZhunClintQ',query:{precustomerno_id: data.precustomerno}})
          }).catch()
        }
    }
})
</script>

