<script>

export default({
    name: 'BClintText',
    data(){
          return{
            prospect: {
              'PreCustomerNo':'',
              'PreCustomerName':'',
              'InsureAim':'',
              'IDType':'',
              'BusiCategory':'',
              'GrpNature':'',
              'IDNo':'',
              'SumNumPeople':'',
              'PreSumPeople':'',
              'Province':'',
