import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Tpregrp from "../components/LocalityClint";
import AddClint from "../components/AddClint";
import StandardClint from "../components/StandardClint";
import Clint from "../components/Clint";
import ClintText from '../components/ClintText';
import ZhunClint from '../components/ZhunClint';
import ZhunClintText from '../components/ZhunClintText';
import ZhunClintQ from '../components/ZhunClintQ';
import BClintText from '../components/BClintText';
import KClintText from '../components/KClintText'

Vue.use(Router)

export default new Router({
  routes: [
    {
      // 路由路径
      path: '/addClint',
      // 路由名称
      name: 'AddClint',
      // 跳转到组件
      component: AddClint
    },
    {
      path: '/clintText',
      name: 'ClintText',
      component: ClintText
    },
    {
      path: '/zhunClint',
      name: 'ZhunClint',
      component: ZhunClint
    },
    {
      path: '/zhunClintText',
      name: 'ZhunClintText',
      component: ZhunClintText
    },
    {
      path: '/zhunClintQ',
      name: 'ZhunClintQ',
      component: ZhunClintQ
    },
    {
      path: '/bClintText',
      name: 'BClintText',
      component: BClintText
    },
    {
      path: '/kClintText',
      name: 'KClintText',
      component: KClintText
    },
    {
      // 路由路径
      path: '/',
      // 路由名称
      name: 'Home',
      // 跳转到组件
      component: Home,
      children:[
        {
          //传参
          path:'/home/tpregrp',
          name:'Tpregrp',
          component:Tpregrp
        },{
          //传参
          path:'/home/standardClint',
          name:'StandardClint',
          component:StandardClint
        },{
          //传参
          path:'/home/clint',
          name:'Clint',
          component:Clint
        },
      ]
    }
  ]
})



