<template>
  <div >
    <div style="background-color: #e4e4e4">
      <el-input v-model="namePhoneId" style="width:280px;margin-top: 10px; margin-left: 5px"   placeholder="请输入准客户名称" />
      <el-button style="width:80px;" icon="el-icon-search" @click="moselect()">搜索</el-button>
      <br>
      <br>
      <el-button style="width: 350px;margin-bottom: 10px;margin-left: 5px;height: 30px; background-color: yellow" @click="$router.push('/addClint')">增加</el-button>
    </div>
    <div>
      <ul v-for="(clint,index) in clints" :key="index"  v-if="clint.customertype == null">
        <li @click="$router.push({name: 'ClintText',query: {precustomerno_id:clint.precustomerno}})">{{clint.precustomername}}</li>
        <li>联系人 ：{{clint.linkmanList[0].linkman }}</li>
        <li><i class="el-icon-phone"></i>{{clint.linkmanList[0].phone }}</li>
        <el-divider></el-divider>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tpregrp",
  data(){
    return{
      clints:[],
      namePhoneId:null
    }
  },
  mounted() {
    this.show();
  },
  methods:{
    show() {
      var vm = this;
      this.axios({
        method: 'get',
        url: 'http://localhost:8081/selectProspectList'
      }).then(function (repos) {
        vm.clints = repos.data
        console.log(repos);
      })
    },
    moselect(){
        var vm = this;
        this.axios({
          method: 'post',
          url: 'http://localhost:8081/selectProspectList?namePhoneId='+this.namePhoneId,
        }).then(function (resp){
          vm.clints=resp.data
          console.log(resp)
        })
    }
  }
}
</script>

<style scoped>

  ul{
    list-style: none;
    position: relative;
    margin-left: -32px;
    width: 340px;
  }
  li{
    text-align: left;
    line-height: 25px;
  }

  .el-form{
    width: 500px;
  }

</style>



