<script>
export default {
  name: "AddClint",
  data() {
    return {
      Province: [],
      City: [],
      County: [],
      ruleForm: {
        'PreCustomerNo': 0,
        'precustomername': '',
        'insureaim': '',
        'idtype': '',
        'busicategory': '',
        'grpnature': '',
        'idno': null,
        'sumnumpeople': '',
        'presumpeople': '',
        'province': '',
        'city': '',
        'county': '',
        'detailaddress': null,
        'linkmanList':[
          {
            'precustomerno': 0,
            'linkman': null,
            'mobile': null,
            'phone': null,
            'birthday': null,
            'depart': null,
            'post': null,
            'email': null,
          },
          {
            'precustomerno': 0,
            'linkman': null,
            'mobile': null,
            'phone': null,
            'birthday': null,
            'depart': null,
            'post': null,
            'email': null,
          }
        ]
      },
      rules: {
        precustomername: [
          { required: true, message: '请输入客户名称', trigger: 'blur' },
          { min: 5, message: '长度不能小于 5 个字符', trigger: 'blur' }
        ]
      }
    };
  },
  mounted(){
      this.show();
  },
  methods: {
    submitForm() {
      var vm = this;
      var data = this.ruleForm
        console.log(data)
        this.axios.post('http://localhost:8081/addProspect',
          this.qs.stringify(data,{arrayFormat: 'indices', allowDots: true}))
          .then(function (response) {
            vm.$router.push({name: 'StandardClint'})
          }).catch()
    },
    //获取所有的省份
  show(){
    var vm = this;
    this.axios({
        method: 'get',
        url: 'http://localhost:8081/selectProvinceListByPid',
      }).then(function (resp){
        vm.Province=resp.data
        console.log(resp)
      })
    },
    //获取所有的市
    getCityList(val){
      var vm = this;
      this.axios({
        method: 'get',
        url: "http://localhost:8081/selectCityListByPid?name="+val,
      }).then(function (resp){
        vm.City=resp.data
        console.log(resp);
      })
    },
    getCountyList(val){
      var vm = this;
      this.axios({
        method: 'post',
        url: "http://localhost:8081/selectCountyListByPid?name="+val,
      }).then(function (resp){
        vm.County=resp.data
        console.log(resp)
      })
    }
  }
}
</script>

